/*
 content
 1- left section
 2- right section animaition  
*/

section.person {
  margin-top: 3rem;
}

/* start left section */
section.person .left-section {
  flex-grow: 1;
}

section.person .parent-avatar.flex {
  align-items: end;
  gap: 0.6rem;
}

section.person .avatar {
  width: 90px;
  border-radius: 50%;
  border: 1px solid var(--blue);
  padding: 1px;
}

section.person .icon-verified {
  color: var(--blue);
  font-size: 1.2rem;
  margin-bottom: 0.3rem;
}

section.person h1.title {
  font-size: 2.85rem;
  line-height: 3.3rem;
  margin: 24px 0;
  color: var(--title);
}

section.person p.sub-title {
  color: var(--subtitle);
  font-size: 0.9rem;
  line-height: 1.65rem;
  margin-bottom: 32px;
}

section.person .icons {
  font-size: 1.2rem;
  gap: 1.5rem;
  color: var(--subtitle);
  transition: 0.3s;
}
section.person .icons .icon:hover {
  cursor: pointer;
  color: var(--icon-hover);
}
/* --end left section */

