/* 
content
1- left section
2- right section
3- media query
*/

main.flex {
    gap: 2.64rem;
    align-items: start;
}

/* 1- start left section */
main.flex section.left-section {
    flex-direction: column;
    gap: 10px;
}

main.flex section.left-section button {
    background-color: rgba(36, 37, 46, 1);
    width: 10rem;
    padding: 0.75rem 0;
    text-align: center;
    font-size: 1.05rem;
    opacity: 0.8;
    border-radius: 5px;
    border: 1px solid transparent;

    transition: 0.3s;
}
main.flex section.left-section button:hover {
    font-size: bold;
    opacity: 1;
    border: 1px solid var(--blue);
}
main.flex section.left-section button.active {
    opacity: 1;
    font-size: bold;
    letter-spacing: 0.6px;
    border: 1px solid var(--blue);
}
.light main.flex section.left-section button.active {
    border: 1px solid #000;
}
/* --end left section */

/* 2- start right section */
main.flex section.right-section {
    justify-content: center;
    flex-grow: 1;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 2rem;
}

main.flex section.right-section article.card {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.16),
        -1px 1px 1px rgba(0, 0, 0, 0.16);
    border-radius: 5px;
    border: 1px solid rgba(93, 188, 252, 0.3);
    transition: 0.3s;
    background-image: linear-gradient(
        rgba(255, 255, 255, 0.05),
        rgba(255, 255, 255, 0.05)
    );
}
.light main.flex section.right-section article.card {
    border: none;
}
main.flex section.right-section article.card:hover {
    border-color: var(--blue);
    cursor: pointer;
}
.light main.flex section.right-section article.card:hover {
    border: none;
    background-color: rgb(245 245 245);
}
main.flex section.right-section article.card img {
    border-radius: 5px;
    width: 266px;
}
main.flex section.right-section article .box {
    padding: 1rem 0.4rem;
}

main.flex section.right-section article .box h1.title {
    color: var(--title);
    font-size: 1.6rem;
}
main.flex section.right-section article .box p.sub-title {
    color: var(--subtitle);
    font-size: 0.8rem;
    margin-top: 0.7rem;
    margin-bottom: 1.2rem;
}
main.flex section.right-section article .box .icons {
    justify-content: space-between;
}
main.flex section.right-section article .box .icons .icon-github,
main.flex section.right-section article .box .icons .icon-link {
    font-size: 1.2rem;
    color: var(--subtitle);
}
main.flex section.right-section article .box .icons .icon-github:hover,
main.flex section.right-section article .box .icons .icon-link:hover {
    color: var(--icon-hover);
    cursor: pointer;
}

main.flex section.right-section article .box a.link {
    color: var(--blue);
    margin-right: 12px;
}
/* --end right section */

/* -3 start media query */
@media (max-width: 600px) {
    main.flex {
        flex-direction: column;
        gap: 2.64rem;
        align-items: center;
    }
    main.flex section.left-section {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
    }

    main.flex section.left-section button {
        width: 8rem;
        padding: 0.5rem 0;
        font-size: 0.9rem;
    }
}
