@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: "Roboto", sans-serif;
    scroll-behavior: smooth;
}

:root {
    --primary: #000;
    --secondary: rgb(24, 24, 27);
    --border: rgba(36, 36, 70, 0.4);
    --title: rgb(244 244 245);
    --subtitle: rgb(161 161 170);
    --bgHeader: rgb(39, 39, 42);
    --blue: rgb(39, 188, 252);
    --icon-hover: #d4d4d8;

    --container-padding: 4rem;
}

.light {
    --primary: rgb(250, 250, 250);
    --secondary: rgb(255, 255, 255);
    --border: rgba(202, 202, 202, 0.518);
    --title: rgb(39 39 42);
    --subtitle: rgb(82 82 91);
    --bgHeader: rgb(245 245 245);
    --blue: rgb(0, 149, 264);
    --icon-hover: #d4d4d8;
}

body {
    background-color: var(--primary);
    color: #fff;
}

.container {
    border: 2px solid var(--border);
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    background-color: var(--secondary);
    padding: 0 var(--container-padding);
}

.divider {
    border-bottom: 1px solid rgb(39 188 252 / 10%);
    margin: 3rem 0;
    width: calc(100% + var(--container-padding) + var(--container-padding));
    margin-left: calc(var(--container-padding) * -1);
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: initial;
    transition: 0.3s;
}

.flex {
    display: flex;
    align-items: center;
}

.border {
    border: 2px solid red;
}

button {
    all: unset;
}

button:hover {
    cursor: pointer;
}

button:active {
    scale: 0.95;
}

.icon-keyboard_arrow_up {
    background-color: rgba(25, 118, 210, 0.85);
    border: 1px solid rgba(255, 255, 255, 0.5);
    height: 2.6rem;
    width: 2.6rem;
    border-radius: 50%;
    text-align: center;
    position: fixed;
    bottom: 2rem;
    right: 3%;
    opacity: 0.4;
}

.icon-keyboard_arrow_up:hover {
    background-color: rgb(25, 118, 210);
}

@media (max-width: 1000px) {
    .container {
        --container-padding: 1rem;
        width: 90%;
        padding: 0 var(--container-padding);
    }

    .divider {
        width: calc(100% + var(--container-padding) + var(--container-padding));
        margin-left: calc(var(--container-padding) * -1);
    }
}

@media (max-width: 600px) {
    .container {
        width: 98%;
        padding: 0 var(--container-padding);
    }
}
