section.contact-us .icon-envelope {
   color: var(--subtitle);
   font-size: 1.8rem;
   margin-right: 1rem;
}

section.contact-us h1.title {
   font-size: 2.1rem;
   color: var(--title);
   margin-bottom: 1rem;
}

section.contact-us p.sub-title {
   color: var(--subtitle);
   margin-bottom: 2rem;
   line-height: 1.6rem;
}
section.contact-us label {
   color: var(--subtitle);
}
section.contact-us .flex {
   justify-content: space-between;
}
section.contact-us #message,
section.contact-us #email {
   all: unset;
   background-color: rgba(63, 63, 70, 0.15);
   border: 1px solid rgb(63 63 70);
   padding: 0.5rem 1rem;
   margin-left: 1rem;
   border-radius: 5px;
   transition: 0.3s;
   font-size: 1.1rem;
}
section.contact-us #message {
   min-height: 9rem;
   resize: vertical;
}
section.contact-us #message:focus,
section.contact-us #email:focus {
   border-color: var(--blue);
}
section.contact-us #message:hover,
section.contact-us #email:hover {
   border-color: var(--blue);
}
section.contact-us .submit {
   display: block;
   background-color: rgba(36, 37, 46, 1);
   padding: 0.75rem 2rem;
   text-align: center;
   font-size: 1.05rem;
   opacity: 0.5;
   border-radius: 5px;
   border: 1px solid;
   transition: 0.3s;
   margin-top: 1.3rem;
   margin-left: 123px;
}
section.contact-us .submit:hover {
   font-size: bold;
   opacity: 1;
   border: 1px solid var(--blue);
}

section.contact-us .email-animation {
   height: 280px;
}

@media (max-width: 600px) {
   .animation {
      display: none;
   }
   section.contact-us form {
      width: 85%;
      display: flex;
      flex-direction: column;
      justify-content: center;
   }

   section.contact-us form div.flex {
      flex-direction: column;
   }

   section.contact-us #message,
   section.contact-us #email {
      width: 100%;
      margin-left: 0;
      padding: 0.7rem 0;
      margin-top: 1rem;
   }

   section.contact-us .submit {
      align-self: center;
      margin-left: 0;
   }
   section.contact-us .form-container {
      justify-content: center;
   }
   section.contact-us .title,
   section.contact-us .sub-title {
      text-align: center;
   }
}

@media (max-width: 800px) {
   section.contact-us .email-animation {
      display: none;
   }
}

.light .email,
.light .textarea {
   color: black;
}
