/* 
content

1- header design
2- button && icon
3- pop up
4- medi query
*/

/* 1- header design */
header.flex {
    justify-content: space-between;
    padding: 0 2.4rem;
    margin-top: 1rem;
}

header.flex nav {
    background-color: var(--bgHeader);
    padding: 0.75rem 1.6rem;
    border-radius: 45px;
}

header ul.flex {
    gap: 16px;
}

header ul li a {
    color: var(--title);
    opacity: 0.9;
    font-size: 0.88rem;
    font-weight: 500;
}

header ul li a:hover {
    color: var(--blue);
    opacity: 1;
}

header button {
    font-size: 1.2rem;
}

header button.menu {
    display: none;
}

/* 2- icon && button */

header .icon-menu,
header button.mode {
    background-color: var(--bgHeader);
    height: 2.4rem;
    width: 2.4rem;
    justify-content: center;
    text-align: center;
    border-radius: 50%;
    color: var(--subtitle);
    border: 1px solid rgba(244, 165, 96, 0.249);
    transition: 0.3s;
}
header .icon-menu:hover,
header button.mode:hover {
    color: var(--title);
    border: 1px solid rgb(244, 165, 96);
}

header .icon-close {
    font-size: 1.3rem;
    color: var(--title);
    transition: 0.3s;
}
header .icon-close:hover {
    color: rgb(244, 165, 96);
}

header .icon-sun {
    color: rgb(244, 165, 96);
}
/* 3- pop up */
header .fixed {
    position: fixed;
    inset: 0;
    z-index: 10;
    background-color: rgba(40, 40, 48, 0.91);
    backdrop-filter: blur(4px);
}

header .fixed ul.modal {
    animation: mymove 0.5s 1;
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem;
    border-radius: 1rem;
    padding: 1rem 2rem;
    background-color: var(--secondary);
}

header .fixed ul.modal li {
    border-bottom: 1px solid var(--border);
    padding-bottom: 0.77rem;
    padding-top: 0.5rem;
}

header .fixed ul.modal li:first-child {
    text-align: right;
    padding-bottom: 0;
    padding-top: 0.2rem;
    border: none;
}

header .fixed ul.modal li:last-child {
    border: none;
}

header .fixed ul.modal li a {
    font-size: 1rem;
}
/* 4- medi query */
@media (max-width: 767px) {
    header.flex nav {
        display: none;
    }

    header button.menu {
        display: block;
    }
}

@keyframes mymove {
    0% {
        scale: 0;
    }
    /* 60% {
    scale: 1.2;
  } */
    100% {
        scale: 1;
    }
}
