footer.flex {
  justify-content: space-between;
  padding-bottom: 1.9rem;
}

footer.flex ul {
  gap: 1rem;
}

footer.flex ul li a {
  color: var(--subtitle);
  font-size: 0.9rem;
  transition: 0.3s;
}
footer.flex ul li a:hover {
  color: var(--blue);
}
footer.flex p {
  color: rgb(113 113 122);
}

@media (max-width: 800px) {
  footer.flex {
    flex-direction: column;
    gap: 2rem;
  }
}
